// pages/enterCommunity/enterCommunity.js
const context = require("../../context/Java110Context.js");
const constant = context.constant;
const factory = context.factory;

export default {
  data() {
    return {
      minDate: new Date().getTime(),
      bindDate: '',
      bindTime: '',
      roomCloums: [],
      roomIdArr: [],
      roomName: "",
      roomId: '',
      roomShow: false,
      columns: ['卧室报修', '管道报修', '客厅报修'],
      typeName: '',
      typeId: '',
      typeShow: false,
      timeShow: false,
      photoList: [],
      bindTel: '',
      context: '',
      bindRepairName: '',
      userId: '',
      storeId: '',
      photos: [],
      communityId: "",
      dateShow: false
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    let that = this;
    context.getRooms().then(res => {
      let arr = res.data.rooms;
      let roomCloums = [];
      let roomIdArr = [];
      arr.map(item => {
        roomCloums.push(item.floorNum + "号楼" + item.unitNum + "单元" + item.roomNum + "室");
        roomIdArr.push(item.roomId);
      });

        that.roomCloums= roomCloums;
        that.roomIdArr= roomIdArr;
        that.userId= res.data.owner.appUserId;
        that.communityId= res.data.owner.communityId;

    });
  },

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {},

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {},

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`;
      } else if (type === 'month') {
        return `${value}月`;
      } else if (type === 'day') {
        return `${value}日`;
      }

      return value;
    },

    bindInput: function (e) {
      console.log('数据监听', e);

      let _that = this;

      let dataset = e.currentTarget.dataset;
      let value = e.detail;
      let name = dataset.name;
      _that[name] = value; 
      console.log(this);
    },
    bindOwner: function (e) {
      // context.getOwner(function(_ownerInfo) {
      // console.log(_ownerInfo, 888888888);
      // if (_ownerInfo) {
      let obj = {
        "typeId": this.typeId,
        "bindRepairName": this.bindRepairName,
        "bindTel": this.bindTel,
        "roomId": this.roomId,
        "photos": [],
        "context": this.context,
        "userId": this.userId,
        "communityId": this.communityId
      };
      let _photos = this.photos;

      _photos.forEach(function (_item) {
        obj.photos.push({
          "photo": _item
        });
      });

      console.log(obj, 3333333333333);
      let msg = "";

      if (obj.roomId == "") {
        msg = "请选择房屋";
      } else if (obj.typeId == "") {
        msg = "请选择报修类型";
      } else if (obj.bindRepairName == "") {
        msg = "请填写报修人";
      } else if (obj.bindTel == "") {
        msg = "请填写手机号";
      } else if (obj.bindDate == "") {
        msg = "请选择预约日期";
      } else if (obj.bindTime == "") {
        msg = "请选择预约时间";
      } else if (obj.context == "") {
        msg = "请填写投诉内容";
      }

      console.log(obj.roomId);

      if (msg != "") {
        wx.showToast({
          title: msg,
          icon: 'none',
          duration: 2000
        });
      } else {
        console.log("提交数据", obj);
        context.request({
          url: constant.url.saveOwnerRepair,
          //  http://hc.demo.winqi.cn:8012/appApi/ownerRepair.saveOwnerRepair 
          header: context.getHeaders(),
          method: "POST",
          data: {
            "repairType": "10001",
            "roomId": "752019100758260005",
            "communityId": "7020181217000001",
            "repairName": "吴学文",
            "tel": "17797173942",
            "context": "服务太差",
            "appointmentTime": "2019-12-14 18:30:30",
            "photos": ['base64....']
          },
          // data:obj, //动态数据
          success: function (res) {
            console.log(res, 99999);
          }
        });
      }
    },
    onChange: function (e) {
      console.log(e);
    },
    onTimeConfirm: function (e) {
      console.log("onConfirm", e);
      this.bindTime= e.detail;
      this.timeShow=false;
    },
    onTimeCancel: function (e) {
      this.timeShow= false;
    },
    chooseTime: function (e) {
      this.timeShow= true;
    },
    onDateConfirm: function (e) {
      console.log("onConfirm", e);
      var oDate = new Date(e.detail * 1),
          oYear = oDate.getFullYear(),
          oMonth = oDate.getMonth() + 1,
          oDay = oDate.getDate();
      console.log("oYear", oYear);
      console.log("oMonth", oMonth);
      console.log("oDay", oDay);
      this.bindDate= oYear + "-" + oMonth + "-" + oDay,
      this.dateShow= false;
    },
    onDateCancel: function (e) {
      this.dateShow= false;
    },
    chooseDate: function (e) {
      this.dateShow= true;
    },
    onTypeConfirm: function (e) {
      console.log("onConfirm", e);
        this.typeName= e.detail.value;
        this.typeId= e.detail.index ? '809001' : '809002';
        this.typeShow= false;
    },
    onTypeCancel: function (e) {
      this.typeShow= false;
    },
    chooseType: function (e) {
      this.typeShow= true;
    },
    onRoomConfirm: function (e) {
      console.log("onConfirm", e);

        this.roomName= e.detail.value;
        this.roomId= this.roomIdArr[e.detail.index];
        this.roomShow= false;

    },
    onRoomCancel: function (e) {
      this.roomShow= false;
    },
    chooseRoom: function (e) {
      this.roomShow= true;
    },
    afterRead: function (event) {
      const {
        file
      } = event.detail;

      let _that = this;

      const {
        photoList = []
      } = this;
      photoList.push(file);
      this.photoList=photoList;
	  
      factory.base64.urlTobase64(file.path).then(function (_baseInfo) {
        _that.photos.push(_baseInfo);
      });
      console.log("data信息：", this);
    },
    removePhoto: function (e) {
      console.log(e.detail.index);
      let _photoList = [];
      this.photoList.forEach(function (item, index) {
        if (index != e.detail.index) {
          _photoList.push(item);
        }
      });
      let _photos = [];
      this.photos.forEach(function (item, index) {
        if (index != e.detail.index) {
          _photos.push(item);
        }
      });
      this.photos= _photos;
      this.photoList= _photoList;
    }
  }
};
